.navbar {
  color: #ffffff;
  background-color: #005662;
  font-family: 'Open Sans', sans-serif;
  top: 0;
  position: sticky;
  position: -webkit-sticky;  /* Safari compatibility */
}

.navbar ul {
  list-style-type: none;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.navbar ul li {
  display: inline;
}

.navbar ul li a {
  color: white;
  cursor: pointer;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  padding: 13px 16px;
}

.navbar ul li a:hover {
  color: #102027;
}
